import { Link as RouterLink } from "react-router-dom";
// material
import { alpha, styled } from "@mui/material/styles";
import {
  Card,
  Grid,
  Typography,
  CardContent,
  CardActionArea,
} from "@mui/material";
// routes
// @types
// import { Post } from '../../../@types/blog';
import { QuestionRepo } from "../../../types/qa/Question";
